import { IS_SSR } from "../constants";
import PropTypes from "prop-types";
import { useEffect } from "react";
import { useLocation } from "react-router-dom";

const RV_AD_ID = "f837d8c96893e793fdbb230cffb628fc";

export function PlatformIOAdZone(props) {
  const location = useLocation();
  useEffect(() => {
    if (IS_SSR) return null;
    try {
      window.reviveAsync[RV_AD_ID].refresh();
    } catch (e) {}
  });
  if (IS_SSR) return null;
  return (
    <div
      key={`${location.key}-${Math.random()}`}
      className="px-1"
      style={{ minHeight: "25px" }}
    >
      <ins data-rvad-zoneid={props.id} data-rvad-id={RV_AD_ID}>
        <span className="invisible">&nbsp;</span>
      </ins>
    </div>
  );
}

PlatformIOAdZone.propTypes = {
  id: PropTypes.number.isRequired,
};
