import { SEARCH_FILTERS_MAP, filterSearchParams } from "./filters";
import { useNavigate, useSearchParams } from "react-router-dom";
import Icon from "../../components/icon";

export default function SearchFiltersApplied() {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const getDisplayName = (filterId, itemId) =>
    (
      ((SEARCH_FILTERS_MAP[filterId] || {}).items || []).find(
        (item) => item.id === itemId
      ) || {}
    ).name;
  const onRemove = (filterId, itemId) => {
    const qs = filterSearchParams({
      searchParams,
      filterId,
      itemId,
      applied: false,
    });
    navigate("/search?" + qs.toString());
  };

  return (
    <div className="hstack flax-wrap">
      {Array.from(searchParams.entries()).map(([filterId, itemId]) =>
        ["q", "s", "t", "tr", "c", "f"].includes(filterId) ? (
          <button
            key={`${filterId}-${itemId}`}
            className="btn btn-sm btn-secondary me-2"
            onClick={() => onRemove(filterId, itemId)}
          >
            <Icon name="x" size="20" />{" "}
            {getDisplayName(filterId, itemId) || itemId}
          </button>
        ) : null
      )}
    </div>
  );
}
