export function makeSearchUrl({ query, page, sort, type, filters }) {
  const qs = new URLSearchParams();
  const qItems = [];
  for (const [key, value] of Object.entries(filters || {})) {
    qItems.push(`${key}:${value.includes(" ") ? `"${value}"` : value}`);
  }
  if (query) qItems.push(query);
  if (qItems.length) qs.set("q", qItems.join(" "));
  if (page) qs.set("p", page);
  if (sort) qs.set("s", sort);
  if (type) qs.set("t", type);
  return "/search?" + qs.toString();
}
