import { Link, Route, Routes, useParams } from "react-router-dom";
import NotFoundMain from "../404/main";
import PackageAuthors from "./authors";
import PackageDependencies from "./dependencies";
import PropTypes from "prop-types";
import { capitalize } from "../../helpers";
import { makeSearchUrl } from "../search/main";

export default function PackageInsights(props) {
  const params = useParams();
  const insight = params["*"];
  const tabs = [
    {
      name: "analytics",
      active: insight === "analytics",
      disabled: true,
    },
    {
      name: "authors",
      active: insight === "authors",
      counter: (props.data.authors || []).length,
    },
    {
      name: "dependencies",
      active: insight === "dependencies",
      counter: (props.data.dependencies || []).length,
    },
    {
      name: "dependents",
      active: insight === "dependents",
      counter: props.data.dependents_count,
    },
  ];
  return (
    <div className="row insights">
      <div className="col-12 col-lg-3 pb-4">
        <div className="list-group nav-insights">
          {tabs
            .filter((tab) => !tab.disabled)
            .map((tab) => (
              <Link
                key={tab.name}
                className={`list-group-item list-group-item-action ${
                  tab.active ? "active" : ""
                }`}
                aria-controls={tab.name}
                aria-selected={tab.active ? "true" : "false"}
                to={
                  tab.name === "dependents"
                    ? makeSearchUrl({
                        filters: {
                          dependency: `${props.data.owner.username}/${props.data.name}`,
                        },
                      })
                    : props.makeSelfUrl({
                        extraPath: `insights/${tab.name}`,
                      })
                }
              >
                {tab.name === "dependents" ? "Used By" : capitalize(tab.name)}
                {tab.counter ? (
                  <span className="badge rounded-pill bg-gray-200 text-dark ms-2 float-end">
                    {tab.counter}
                  </span>
                ) : null}
              </Link>
            ))}
        </div>
      </div>
      <div className="col col-lg-9">
        <Routes>
          <Route
            path="authors"
            element={<PackageAuthors data={props.data} />}
          />
          <Route
            path="dependencies"
            element={<PackageDependencies data={props.data} />}
          />
          <Route path="*" element={<NotFoundMain />} />
        </Routes>
      </div>
    </div>
  );
}

PackageInsights.propTypes = {
  data: PropTypes.shape({
    owner: PropTypes.shape({
      username: PropTypes.string.isRequired,
    }).isRequired,
    name: PropTypes.string.isRequired,
    dependents_count: PropTypes.number.isRequired,
    authors: PropTypes.array,
    dependencies: PropTypes.array,
  }).isRequired,
  makeSelfUrl: PropTypes.func.isRequired,
};
