import Humanize from "../../components/humanize";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { makeSearchUrl } from "../search/main";

export default function PackageAuthors(props) {
  if (!props.data.authors || !props.data.authors.length) {
    return (
      <div className="card text-center">
        <div className="card-body">
          <h5 className="card-title">No Authors</h5>
        </div>
      </div>
    );
  }
  return (
    <>
      <h3 className="border-bottom pb-1">Authors</h3>
      <table className="table table-striped mt-4">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Name</th>
            <th scope="col">E-Mail</th>
            <th scope="col">Homepage</th>
            <th scope="col">Maintainer</th>
          </tr>
        </thead>
        <tbody>
          {props.data.authors.map((author, index) => (
            <tr key={index}>
              <th scope="row">{index + 1}</th>
              <td>
                <Link to={makeSearchUrl({ filters: { author: author.name } })}>
                  {author.name}
                </Link>
              </td>
              <td>
                <div className="text-truncate">
                  {author.email ? (
                    <a href={`mailto:${author.email}`}>{author.email}</a>
                  ) : null}
                </div>
              </td>
              <td>
                <div className="text-truncate">
                  {author.url ? (
                    <a
                      href={author.url}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <Humanize type="url" value={author.url} />
                    </a>
                  ) : null}
                </div>
              </td>
              <td>{author.maintainer ? "Yes" : null}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </>
  );
}

PackageAuthors.propTypes = {
  data: PropTypes.shape({
    authors: PropTypes.arrayOf(
      PropTypes.shape({
        name: PropTypes.string.isRequired,
        email: PropTypes.string,
        url: PropTypes.string,
        maintainer: PropTypes.bool,
      })
    ),
  }).isRequired,
};
