import Icon from "../../components/icon";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { makePackageUrl } from "../package/main";

export default function PackageDependencies(props) {
  if (!props.data.dependencies || !props.data.dependencies.length) {
    return (
      <div className="card text-center">
        <div className="card-body">
          <h5 className="card-title">No Dependencies</h5>
        </div>
      </div>
    );
  }
  return (
    <>
      <h3>Dependencies</h3>
      <ul className="list-group mt-3">
        {props.data.dependencies.map((item) => (
          <li key={item.id} className="list-group-item">
            <Icon name={`package-${item.type}`} />
            <Link
              to={makePackageUrl({
                type: item.type,
                ownername: item.owner.username,
                name: item.name,
              })}
              className="ms-2"
            >
              {item.owner.username}/{item.name}
            </Link>
          </li>
        ))}
      </ul>
    </>
  );
}

PackageDependencies.propTypes = {
  data: PropTypes.shape({
    dependencies: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.number.isRequired,
        type: PropTypes.string.isRequired,
        owner: PropTypes.shape({
          username: PropTypes.string.isRequired,
        }).isRequired,
        name: PropTypes.string.isRequired,
      })
    ),
  }).isRequired,
};
