import { Link, useParams } from "react-router-dom";

export default function NotFoundMain() {
  const params = useParams();
  if (params["*"] !== "404.html") {
    window.location.href = "/404.html";
  }
  return (
    <main className="main-404">
      <div className="container min-vh-1002 text-center py-5">
        <div className="w-md-75 w-lg-65 mx-md-auto text-center">
          <h1>Page not found!</h1>
          <p className="text-muted">
            It seems we can't find the page you are looking for.
          </p>
          <Link to="/" className="btn btn-lg btn-primary my-3">
            Go to homepage
          </Link>
        </div>
        <img
          src={process.env.PUBLIC_URL + "/images/404.svg"}
          alt="Page not found"
          className="w-100 w-md-75 w-lg-65 mt-4"
        />
      </div>
    </main>
  );
}
