import { IS_SSR, REGISTRY_BADGES_ENDPOINT } from "../../constants";
import { useEffect, useState } from "react";
import { CopyToClipboard } from "react-copy-to-clipboard";
import PropTypes from "prop-types";
import { enableBStooltips } from "../../helpers";

export default function PackageBadge(props) {
  if (IS_SSR) return null;
  const title = "PlatformIO Registry";
  const badgeImgUrl = `${REGISTRY_BADGES_ENDPOINT}/packages/${props.data.owner.username}/${props.data.type}/${props.data.name}.svg`;
  return (
    <div className="d-flex justify-content-between flex-wrap">
      <div className="pe-2">
        <img src={badgeImgUrl} alt={title} />
      </div>
      <div>
        <div className="d-flex">
          <div>
            <CopyCode
              title="Copy HTML code"
              buttonValue="HTML"
              code={`<a href="${props.url}"><img src="${badgeImgUrl}" alt="${title}" /></a>`}
            />
          </div>
          <div className="px-1"></div>
          <div>
            <CopyCode
              title="Copy Markdown code"
              buttonValue="Markdown"
              code={`[![${title}](${badgeImgUrl})](${props.url})`}
            />
          </div>
        </div>
      </div>
    </div>
  );
}

PackageBadge.propTypes = {
  data: PropTypes.shape({
    type: PropTypes.string.isRequired,
    owner: PropTypes.shape({
      username: PropTypes.string.isRequired,
    }).isRequired,
    name: PropTypes.string.isRequired,
  }).isRequired,
  url: PropTypes.string.isRequired,
};

function CopyCode(props) {
  useEffect(() => {
    enableBStooltips();
  });
  const [copied, setCopied] = useState(false);
  const onCopy = () => {
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  return (
    <CopyToClipboard text={props.code} onCopy={onCopy}>
      <button
        type="button"
        className="btn btn-link btn-sm p-0"
        data-bs-toggle="tooltip"
        title={props.title}
      >
        {copied ? (
          <span className="text-success">{props.buttonValue}</span>
        ) : (
          props.buttonValue
        )}
      </button>
    </CopyToClipboard>
  );
}

CopyCode.propTypes = {
  title: PropTypes.string.isRequired,
  buttonValue: PropTypes.string.isRequired,
  code: PropTypes.string.isRequired,
};
